 .front {\n  box-shadow: 0 0 8px 0px black;\n  border-radius: 1rem;\n  position: absolute;\n  height: 100%;\n  width: 100%;\n  backface-visibility: hidden; \n}\n\n.page-header .photo .back {\n  background: url(./assets/Vagabond.jpeg) no-repeat center 5% / cover; \n  transform: rotateY(-180deg);\n}\n\n.page-header .photo .front {\n  background: url(./assets/Vagabond-colored-1.png) no-repeat center 20%/ cover;\n  transform: rotateY(0deg);\n}\n\n.photo-wrapper:hover .photo,\n.photo-wrapper:active .photo {\n  transform: rotateY(180deg);\n}\n\n.page-header .info .photo h1 {\n  position: absolute;\n  inset: auto 0 0;\n  text-align: center;\n  color: var(--bg);\n  font-size: 2.5rem;\n}\n\n.page-header .info .photo .front h1 {\n  top: 0;\n  color: var(--text);\n}\n\n.page-header .info h1 {\n  font-size: 3rem;\n}\n\n.page-header .info p {\n  font-size: 2rem;\n}\n\n.page-header .info .links{\n  position: absolute;\n  bottom: 1rem;\n  right: 1rem;\n  height: 4rem;\n}\n\n\n\n.info .links svg {\n  will-change: transform;\n  transition: ease-out .15s transform;\n  height: 100%;\n}\n\n.info .links svg:hover {\n  transition: ease-in .15s transform;\n  transform: scale(1.1);\n}\n\nmain, .page-footer {\n  padding: 4rem;\n}\n\nmain h1,\n.page-footer h1 {\n  font-size: 3rem;\n  margin-bottom: 1rem;\n}\n\n.cards {\n  display: grid;\n  grid-template-columns: repeat( auto-fit, minmax(25rem, 1fr) );\n  gap: 3rem;\n  margin-top: 2rem;\n}\n\n.card {\n  box-shadow: 0 0 16px -4px black;\n  border-radius: 1rem;\n  overflow: hidden;\n  will-change: transform;\n  transition: ease-in-out 0.2s;\n  position: relative;\n  aspect-ratio: 1.2;\n  display: grid;\n  grid-template-rows: 2fr 1fr;\n  max-height: 80vh;\n}\n\n.card:hover {\n  transform: scale(1.02);\n}\n\n.card img {\n  filter: grayscale(1);\n  width: 100%;\n  object-fit: cover;\n  object-position: center;\n  border-bottom: 1px solid rgb(207, 207, 207);\n  transition: ease-in-out 0.2s;\n  will-change: filter;\n  min-height: 0;\n  height: 100%;\n}\n\n.card:hover img {\n  filter: grayscale(0);\n}\n\n.card .info {\n  display: grid;\n  grid-template-areas: \n    \"name links\"\n    \"desc desc\";\n  grid-template-columns: 1fr auto;\n  grid-template-rows: auto 1fr;\n  row-gap: 0.4rem;\n  padding: 1.2rem 1.5rem 1.5rem;\n  min-height: 0;\n}\n.card .name {\n  grid-area: name;\n  line-height: 1.2;\n}\n.card .links {\n  grid-area: links;\n  height: 100%;\n  display: grid;\n  grid-auto-flow: column;\n  gap: 1.5rem;\n}\n\n.card .links a {\n  height: 100%;\n  aspect-ratio: 1;\n}\n\n.card .description {\n  grid-area: desc;\n  font-size: 1.15rem;\n  overflow: hidden;\n  height: 100%;\n  mask-image: linear-gradient(to bottom, black 80%, transparent 100%);\n  -webkit-mask-image: linear-gradient(to bottom, black 80%, transparent 100%);\n}\n\n.page-footer {\n  background-color: var(--primary);\n  display: grid;\n  grid-template-columns: 1fr auto;\n  color: var(--bg);\n  padding-block: 5rem;\n  gap: 2rem;\n  align-items: center;\n}\n\n.info a {\n  display: inline-block;\n  height: 100%;\n}\n\n.page-footer .contacts {\n  position: relative;\n}\n\n.page-footer p {\n  font-size: 1.8rem;\n  margin-bottom: 2rem;\n}\n\n.page-footer svg {\n  fill: var(--bg);\n}\n\n.page-footer a {\n  color: var(--bg);\n  display: flex;\n  align-items: center;\n  gap: 0.5rem;\n  margin-bottom: 1rem;\n  font-size: 1.5rem;\n  height: 3rem;\n}\n\n.page-footer a svg {\n  height: 3rem;\n}\n\n.page-footer a .email {\n  margin: 0;\n}\n\n.page-footer img {\n  border-radius: 1rem;\n  height: 38rem;\n  box-shadow: 0 0 16px 0 black;\n}\n\n\n\n@media (max-width: 1170px) {\n  .page-header .photo-wrapper {\n    transform: translate(calc(1.4rem - 1.1vw), calc(0.5rem - 1px)) scale(1.3);\n  }\n  main, .page-footer {\n    padding: 3rem 6rem;\n  }\n\n  .page-header .info .links,\n  .page-footer a {\n    height: 3rem;\n  }\n\n  .page-footer img {\n    border-radius: 1rem;\n    height: 30rem;\n    box-shadow: 0 0 16px 0 black;\n  }\n  .page-footer p {\n    font-size: 1.5rem;\n  }\n\n  .page-header .info .photo h1 {\n    font-size: 2rem;\n  }\n}\n\n@media (max-width: 800px) {\n  main h1,\n  .page-footer h1,\n  .page-header .info h1 {\n    font-size: 2.5rem;\n  }\n  .page-header .info p {\n    font-size: 1.5rem;\n  }\n  main, .page-footer {\n    padding: 3rem 2rem;\n  }\n  .page-header::before {\n    --offset: calc(tan(var(--distortion)) * 300%);\n  }\n  .page-header .info {\n    margin-top: 4rem;\n    padding: 0 0 3.5rem;\n    width: 28rem;\n    background-color: transparent;\n    box-shadow: none;\n  }\n\n  .page-header .info .links {\n    bottom: 0rem;\n    right: -0.5rem;\n  }\n  .page-header .photo-wrapper {\n    position: relative;\n    right: 0;\n    left: 0;\n    width: auto;\n    height: 40rem;\n    transform: scale(1);\n    perspective: 1200px;\n    margin-bottom: 8rem;\n  }\n\n  .page-header .info .placeholder {\n    position: absolute;\n    background-color: var(--bg);\n    z-index: -1;\n    right: 0;\n    width: auto;\n    left: 0;\n    top: 48rem;\n    height: auto;\n    max-height: none;\n    bottom: 0;\n    transform: scale(1.15);\n    border-radius: 1rem;\n    box-shadow: 0 0 16px -3px black;\n  }\n\n  .page-footer {\n    grid-template-columns: auto;\n  } \n\n  \n  \n  .page-footer .photo {\n    justify-self: center;\n    order: -1;\n  }\n  .page-header .info .photo h1 {\n    font-size: 2.5rem;\n  }\n}\n\n@media (max-width: 500px) {\n  .card {\n    aspect-ratio: 0.8;\n    grid-template-rows: 2fr 1fr;\n  }\n  .page-footer img {\n    height: 24rem;\n    border-radius: 1rem;\n    box-shadow: 0 0 16px 0 black;\n  }\n\n  .page-header .info .placeholder {\n    transform: scale(0.95);\n    top: 44rem;\n  }\n  .page-header .info {\n    padding: 1.2rem 1.2rem 4rem;\n  }\n  .page-header .photo-wrapper {\n    margin-bottom: 4rem;\n  }\n\n  .page-header .info .links {\n    bottom: 1rem;\n    right: 1rem;\n  }\n  .page-footer h1 {\n    font-size: 2rem;\n  }\n\n  .page-footer p {\n    font-size: 1.4rem;\n    margin-bottom: 1rem;\n  }\n\n  main, .page-footer {\n    padding: 3rem 0.5rem;\n  }\n}\n\n@media (hover: none) {\n  .card img {\n    filter: none;\n  }\n}\n"],"sourceRoot":""}]);
